<template lang="html">
  <div class="application">
    <br>
    <br>
    <p>{{msg}}</p>
  
    <br>
    <br>
    <button type="button" name="button" @click="openCamera">调用相机</button>
    <br>
    <br>
    <br>
    <button type="button" name="button" @click="openMa">扫码</button>
  </div>
</template>

<script>
export default {
  name:'application',
  data:function(){
    return {
      msg:'application'
    }
  },
  methods:{
    openCamera:function(){
      var tag=this;
      summer.openCamera({
        callback : function(args){
          tag.msg=args;
        }
      });
    },
    openMa:function(){
      summer.openScanner({
          callback : function (args){
            $summer.alert(args)
          }
      });
    }
  }
}
</script>

<style lang="css">
</style>



