<template lang="html">
  <div class="application">
    <button type="button" name="button" @click="http1">本地获取json</button>

    <button type="button" name="button" @click="http2">远程获取json</button>
    <button type="button" name="button" @click="http3">远程跨域获取jsonp</button>
    <p>{{msg}}</p>
  </div>
</template>

<script>
export default {
  name:'companyMade',
  data:function(){
    return {
      msg:'CompanyMade'
    }
  },
  methods:{
    http1:function(){
      this.$http.get('./static/mockData.json').then(res=>{
        this.msg=res;
        },err=>{
        this.msg=res;
      });
    },
    http2:function(){
      this.$http.get('http://hujinlianrong.com/public/data/atrList.php').then(res=>{
        this.msg=res;
        },err=>{
        this.msg=res;
      });
    },
    http3:function(){
      this.$http.jsonp('https://api.douban.com/v2/movie/top250?count=10p').then((res)=>{
        this.msg=res;
        },err=>{
        this.msg=res;
      });
    }

  }
}
</script>

<style lang="css">
</style>



